export default {
	data() {
		return {
			list:[],
			picUrl: this.$pictureUrl
		}
	},
	onLoad() {
		this.selCollectById();
	},
	methods: {
		selCollectById() { //我的收藏
			uni.request({
				url: this.$serverUrl + '/api/collect/selCollectById',
				method: 'POST',
				header:{
					'token':uni.getStorageSync('userinfo').token
				},
				data: {
					member_id: uni.getStorageSync('userinfo').member_id
				},
				success: (ret) => {
					if (ret.statusCode !== 200) {
						console.log('请求失败', ret);
						return;
					}
					if (ret.data.code == 0) {
						this.list = ret.data.data.list;
						console.log(this.list)
					} 
					if (ret.data.code == 2) {
						// 气泡提示
						uni.showToast({
							title: '数据异常，请重新登录',
							duration: 2000,
							success() {
								uni.reLaunch({
									url: '../login/login'
								});
							}
						});
						
						
					} 
				}
			});
		},
		delCollect(collect_id) { // 触发提示框
			uni.showModal({
				title: '提示',
				content: '确定删除',
				confirmColor:'#FF4400',
				success:(res) => {
					if (res.confirm) {
						// 删除收藏
						this.delCollectDo(collect_id);
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
			
		},
		delCollectDo(collect_id) { // 删除收藏
			uni.request({
				url: this.$serverUrl + '/api/collect/delCollect',
				method: 'POST',
				header:{
					'token':uni.getStorageSync('userinfo').token
				},
				data: {
					collect_id: collect_id
				},
				success: (ret) => {
					if (ret.statusCode !== 200) {
						console.log('请求失败', ret);
						return;
					}
					if (ret.data.code == 0) {
						// 刷新
						this.selCollectById();
					} 
					if (ret.data.code == 2) {
						// 气泡提示
						uni.showToast({
							title: '数据异常，请重新登录',
							duration: 2000,
							success() {
								uni.reLaunch({
									url: '../login/login'
								});
							}
						});
					} 
				}
			});
		}
	}
}
